.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#hiddenTextBox {
  opacity: 0;
  pointer-events: none;
  width: 0;
  height: 0;
  margin: 0;
  padding: 0;
}

#wordDefinition {
  font-size: 1.5em;
  margin: 0;
}

.wordContainer {
  display: flex;
  justify-content: center;
  margin: 10px
}

.charContainer {
  width: 30px;
  height: 30px;
  padding: 10px;
  margin: 0 5px;
  font-size: 1.2em;
  font-weight: 700;
}

.charContainer.background-white {
  background-color: white;
}

.charContainer.background-orange {
  background-color: orange;
}

.charContainer.background-green {
  background-color: lightgreen;
}

.charContainer.border-grey {
  border: 2px solid lightgrey;
}

.charContainer.border-black {
  border: 2px solid black;
}

.keyboardContainer {
  display: flex;
  justify-content: center;
}

.keyboardButton {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-color: lightgrey;
  padding: 10px;
  margin: 5px;
  border-radius: 10px;
  border: none;
  font-weight: 700;
  overflow: auto;
}

.keyboardButton:hover {
  background-color: grey;
  cursor: pointer;
}

.keyboardButton.wide {
  width: 100px;
  height: 50px;
}

.keyboardButton.large {
  width: 50px;
  height: 50px;
}

.keyboardButton.small {
  width: 40px;
  height: 40px;
}

